.single-country-container {
  width: 800px;
  padding: 40px;
  border-radius: 10px;
  background-color: #bbd9ed;
  text-align: center;
  border: 2px solid #007bff;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin: auto;
}

/* Title style */
.single-country {
  font-size: 25px;
  color: #2bb948;
  margin-bottom: 20px;
}

/* Description style */
p {
  font-size: 15px;
  color: #3d4b99;
  margin-bottom: 15px;
  font-family: Georgia, "Times New Roman", Times, serif;
}

/* Image style */
.img {
  display: block;
  width: 80%;
  max-height: 300px; /* Limit the height of the image */
  margin: 0 auto;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); /* Add a subtle shadow */
}

.back-button {
  background-color: #081a62;
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
  color: #fff;
  border: 5px solid #4292e3;
  padding: 10px 20px;
  font-size: 18px;
  cursor: pointer;
  margin: 0 10px;
  border-radius: 20px;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

/* Button hover effect */
.back-button:hover {
  background-color: #2f043b;
  transform: scale(1.05);
}
